.shelf:hover {
  cursor: pointer;
  -webkit-animation: bounce 0.4s ease;
          animation: bounce 0.4s ease;
  -webkit-animation-delay: 0.04s;
          animation-delay: 0.04s;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1; }
  .shelf:hover ~ .bookOne {
    -webkit-animation: bounce 0.2s ease;
            animation: bounce 0.2s ease;
    -webkit-animation-delay: 0.04s;
            animation-delay: 0.04s; }
  .shelf:hover ~ .bookTwo {
    -webkit-animation: bounce 0.6s ease;
            animation: bounce 0.6s ease;
    -webkit-animation-delay: 0.04s;
            animation-delay: 0.04s; }
  .shelf:hover ~ .bookThree {
    -webkit-animation: bounce 0.1s ease;
            animation: bounce 0.1s ease;
    -webkit-animation-delay: 0.04s;
            animation-delay: 0.04s; }
  .shelf:hover ~ .bookFour {
    -webkit-animation: bounce 0.4s ease;
            animation: bounce 0.4s ease;
    -webkit-animation-delay: 0.04s;
            animation-delay: 0.04s; }
  .shelf:hover ~ .bookshelf {
    -webkit-animation: lift 1s ease-in-out;
            animation: lift 1s ease-in-out;
    -webkit-transform-origin: 30% 30%;
            transform-origin: 30% 30%; }

